body{
  --background: white;
  --text-color: #212324;
  --black: #000;
  --border-color: rgba(0,0,0,.13);
  --z-depth-1: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
  --grey-color: #777;
  --second-bg: rgb(0,0,0,.04);
  --tab-text-color: #5f6368;
  --tab-active-color: #1A73E8;
  background-color: var(--background);
  padding: 0; 
  margin: 0;
  font-family: 'Roboto', sans-serif;
  color: var(--text-color);

}
.currency{
  max-width: 690px;
  margin: 1rem auto;
  padding: 1rem;
  border: 1px solid var(--border-color);
  border-radius: 8px;

}
.currencyItem {
  padding: 0 3rem;
}

.currency__header--title{
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 2rem;

}
.number {
  font-family: 'Oswald', sans-serif;
}
.mdTable{
  max-width: 700px;
  width: 100%;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  box-shadow: var(--z-depth-1);
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}
.mdTable__header{
  display: flex;
  border-bottom: 1px solid var(--border-color)
}

.mdTable__header--row{
  display: grid;
  grid-template-columns: 3fr 1fr 1fr;
  width: 100%;
}
.mdTable__header--th{
  padding: .9rem;
  text-align: left;
  color: var(--grey-color);
  font-weight: 500;
}
.mdTable__header--td{
  padding: .9rem;
  text-align: left;
  font-weight: 400;
  color: var(--black);
}
.mdTable__header--th:not(:first-child),
.mdTable__header--td:not(:first-child) {
  text-align: right;
}
.mdTable__inner--row:nth-child(2n){
  background-color: var(--second-bg)
}
.align-right{
  text-align: right;
}
.switchTheme__img{
  display: block;
  cursor: pointer;
  width: 24px;
  height: 24px;
  display: block;
  z-index: 2;
}
.switchTheme__img--night{
  filter: invert(1)
}
@media(max-width: 468px) {
  .mdTable__header--row{
    grid-template-columns: 2fr 1fr 1fr;
  }
  .mdTable__header--td{
    font-size: 0.9rem;
  }
}
@media(max-width: 601px) {
  .currency{
    margin-top: 0;  
  }
}