.tabs__inner--item{
    display: none;
}
.tabs__inner--active{
    display: block;
}

.tabs__header{
    margin: 0;
    padding: 0;
    display: flex;
}
.tabs__header--item{
    list-style-type: none;
    display: flex;
    position: relative;
}
.tabs__header--link{
    padding: .5rem 1rem;
    padding-bottom: calc(.5rem + 3px);
    text-decoration: none;
    color: var(--tab-text-color); 
    font-weight: 500;
}
.tabs__header--link::-moz-focusring{
    outline: none !important;
    border: none;
}
 
.tabs__header--link:hover,
.tabs__header--link:focus,
.tabs__header--active{
    outline: none !important;
    color: var(--tab-active-color);
    border: 0;

}


.tabs__header--active::after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 0 5%;
    width: 90%;
    height: 3px;
    border-radius: 3px 3px 0 0;
    background-color: var(--tab-active-color);
}

